const fields = data.fields;
const summ = (day, fields) => {
    let dd = [];
    let summ = 0;
    fields.forEach(
        item => {
            if (item.filter(item1 => item1.field === 'datePosting' && item1.value === day ).length >0) dd.push(
                {
                    summ: item.filter( t => t.field === 'type' )[0].value *  item.filter( t => t.field === 'summ' )[0].value
                }
            );
        }
    );
    dd.forEach( item => summ += item.summ );
    return summ;
}
const getClass = (summ) => {
    if (summ<0) return "minus-day";
    if (summ>0) return "plus-day";
    if (summ===0) return "netral-day";
}

const ff = () => {
    let temp = [];
    fields.forEach(item => temp.push(item.filter(item1 => item1.field === 'datePosting')[0].value));
    temp = temp.filter((value, index, self) => self.indexOf(value) === index).map( date => ({ date, summ: summ(date, fields), class: getClass(summ(date, fields)) }) );
    return temp;
}

const customCSSclasses = {
    "minus-day": ff().filter( item => item.class === 'minus-day' ).map( item => item.date ),
    "plus-day": ff().filter( item => item.class === 'plus-day' ).map( item => item.date ),
    "netral-day": ff().filter( item => item.class === 'netral-day' ).map( item => item.date ),
}

const SimpleYearColendar = ({state, dispatch, classes}) =>
    <Paper id='calendar' className={classes.root}>
        <CalendarControls
            className={classes.table}
            year={2018}
            showTodayButton={true}
            onPrevYear={() => {
            }}
            onNextYear={() => {
            }}
            goToToday={() => {
            }}
        />
        <div className={classes.root}>
            <Calendar
                year={2018}
                onPickDate={(date) => alert(date)}
                firstDayOfWeek={2}
                customClasses={customCSSclasses}
            />
        </div>
    </Paper>

export default connector(lifecycle(methods)(withStyles(styles)(SimpleYearColendar)));